* {
  box-sizing: border-box;
}

body{
  margin: 0;
}

main {
  padding: 36px;
}

.header {
  display:flex;
  align-items:center;
  height:65px;
  background: linear-gradient(90deg, #421798 1.18%, #517183 100%);
  color:white;
  padding: 20px;
}
.header-img{
  height: 100%;
  margin-right:10px;
  border-radius: 40% 30% 40% 20%;
}
.header-title{
  font-size: 1.25rem;
  margin-right: auto;
}
.header-project{
  font-size: 12px;
  font-weight: 500;
}

.form{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 20px;
  margin-bottom: 20px;
}
.form-input{
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}
.form-button{
  grid-column: 1 / -1;
  border-radius: 5px;
  background: linear-gradient(90deg, #421798 1.18%, #517183 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.meme {
  position: relative;
}

.meme-image{
  max-width: 100%;
  border-radius: 3px;
}


.meme-text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}